<template>
  <div class="article" :title="url" @click="click">
    <h3 class="mb-0">{{ title }}</h3>
    <span class="w-100 text-end mb-2 fst-italic">–{{ author }}</span>
    <p class="ps-3">{{ description }}</p>
    <div class="gradient" />
  </div>
</template>

<script>
export default {
  props: ["item"],
  computed: {
    title() {
      return this.item.title;
    },
    date() {
      return this.item.date;
    },
    author() {
      return this.item.author;
    },
    description() {
      return this.item.description;
    },
    url() {
      return this.item.url;
    },
  },
  methods: {
    click() {
      window.open(this.url, "_blank");
    },
  },
};
</script>

