export default {
  stripHtml(html) {
    let tmp = document.createElement("div");
    tmp.innerHTML = html;
    return tmp.textContent || tmp.innerText || "";
  },
  fetchRss(url, completion) {
    fetch(url)
      .then((response) => response.text())
      .then((str) => new window.DOMParser().parseFromString(str, "text/xml"))
      .then((data) => {
        let r = Array.from(data.getElementsByTagName("item")).map((i) => {
          window.temp1 = i;
          return {
            title: i.getElementsByTagName("title")[0].childNodes[0].nodeValue,
            date: new Date(
              i.getElementsByTagName("pubDate")[0].childNodes[0].nodeValue
            ),
            author: i.getElementsByTagName("dc:creator")[0].childNodes[0]
              .textContent,
            description: this.stripHtml(
              i.getElementsByTagName("description")[0].childNodes[0].textContent
            ),
            url: i.getElementsByTagName("guid")[0].childNodes[0].nodeValue,
          };
        });
        r.sort((a, b) => b.date - a.date);
        completion(r);
      });
  },
};
