<script>
// @ is an alias to /src
import FeedArticle from "@/components/FeedArticle.vue";
import axios from "axios";
import rss from "@/services/rss.js";

export default {
  name: "Home",
  components: { FeedArticle },
  data() {
    return {
      animationFrame: 0,
      animationLength: 16,
      interval: null,
      videoSrc: "",
      cityInput: "",
      emailInput: "",
      showBuyFields: false,
      showBuyLocation: false,
      toast: null,
      sleepArticles: [],
      tinnitusArticles: [],
    };
  },
  methods: {
    watchVideo(vID) {
      this.videoSrc = `https://www.youtube.com/embed/${vID}?autoplay=1&modestbranding=1&showinfo=0`;
    },
    closeModal() {
      this.videoSrc = "";
    },
    showBuyCollapse() {
      // this.$refs.collapseBuyAfter.scrollIntoView({});
      setTimeout(() => {
        this.$refs.collapseBuy.scrollIntoView({});
      }, 1);
    },
    selectCity(c) {
      this.showBuyLocation = c == "";
      this.showBuyFields = true;
      this.cityInput = c;
      setTimeout(() => {
        this.showBuyCollapse();
      }, 1);
    },
    sendBuy() {
      this.toast.show();
      axios.post("/api/waitlist", {
        email: this.emailInput,
        city: this.cityInput,
      });
    },
  },
  mounted() {
    this.interval = setInterval(() => {
      this.animationFrame = (this.animationFrame + 1) % this.animationLength;
    }, 600);
    this.toast = new window.bootstrap.Toast(this.$refs.enteredToast);
    rss.fetchRss(
      process.env.BASE_URL + "schlafen.rss",
      (r) => (this.sleepArticles = r)
    );
    rss.fetchRss(
      process.env.BASE_URL + "tinnitus.rss",
      (r) => (this.tinnitusArticles = r)
    );
  },
  unmounted() {
    this.interval && clearInterval(this.interval);
  },
};
</script>

