import { createRouter, createWebHashHistory } from "vue-router";
import Home from "../views/Home.vue";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/sleep",
    name: "Sleep",
    // route level code-splitting
    // this generates a separate chunk (sleep.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "sleep" */ "../views/Sleep.vue"),
  },
  {
    path: "/tinnitus",
    name: "Tinnitus",
    // route level code-splitting
    // this generates a separate chunk (tinnitus.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "tinnitus" */ "../views/Tinnitus.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
